/* ******Header******** */
.nav_logo h2 {
  font-style: normal;
  font-weight: 500;
  font-weight: 500;
  font-size: 1.4em;
  color: #0a0e17;
  margin: 7px 0;
}
.head_menu {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  position: relative;
  color: #0a0e17;
}
.head_menu.active {
  color: #e84a56;
}
.head_menu:focus {
  outline: none;
  box-shadow: none;
}

.head_menu .dropdown_arrow {
  content: "";
}
.head_menu .dropdown_arrow::before {
  content: "";
  position: absolute;
  top: 104%;
  left: 50%;
  transform: translate(-50%, 0);
  border: 4px solid #e84a56;
  border-top-color: transparent;
  border-left-color: transparent;
  border-right-color: transparent;
  z-index: 1;
  width: 100%;
}
.head_menu .dropdown-menu {
  position: absolute;
  z-index: 10;

  margin: 0.125rem 0 0;
  min-width: 10rem;
  padding: 0.5rem 0;

  font-size: 1rem;
  color: #212529;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  left: 0;
  top: 55px;
}

.head_menu .dropdown-item {
  display: block;
  width: 100%;
  padding: 0.25rem 1.5rem;
  clear: both;
  font-weight: 400;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
  position: relative;
  text-transform: capitalize;
}
