/* .banner .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #01052403;
} */
.banner .slick-prev,
.banner .slick-next {
  font-size: 0px;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 39px;
  height: 43px;
  padding: 0;
  cursor: pointer;
  /* color: #212529; */
  z-index: 10;
  border: none;
  outline: none;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.banner .slick-prev {
  left: 35px;
  background-image: url("./Assets/Images/lt_arr.png");
}
.banner .slick-next {
  right: 35px;
  background-image: url("./Assets/Images/rt_arr.png");
}
.banner .slick-prev:before,
.banner .slick-next:before {
  color: #ffffff;
}

.banner .slick-dots li button:before {
  font-family: "slick";
  font-size: 16px;
  line-height: 20px;
  position: absolute;
  top: -42px;
  left: 0;
  width: 18px;
  height: 18px;
  content: "•";
  text-align: center;
  opacity: 0.45;
  color: #e84a56;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.banner .slick-dots li.slick-active button:before {
  opacity: 0.75;
  color: #e84a56;
}
