/* chat call css */

li.log_time {
  font-size: 75%;
  margin: 1rem auto;
  text-align: center;
  position: relative;
}

li.log_time:after {
  position: absolute;
  content: " ";
  left: 0;
  bottom: 7px;
  width: 100%;
  height: 100%;
  border-bottom: 2px solid #eaeaea;
}

.log_time span:last-child {
  background: #fff4f4;
  position: relative;
  z-index: 1;
  padding: 0 6px;
  border-radius: 4px;
  margin: 0;
}

/* *************video call modal*************** */
.modal-dialog.video-modal {
  position: relative;
  width: 100%;
  /* margin: 0.5rem; */
  pointer-events: none;
  max-width: 716px;
  object-fit: cover;
}
/* video */
._large_video {
  position: relative;
  border-radius: 13px;
  width: auto;
  height: clamp(250px, 62%, 370px);
}
._large_video video {
  width: 100%;
  height: 100%;
  max-height: 400px;
  /* border-radius: 8% 8% 0 0; */
  /* margin-top: 1em; */
}
._large_video img {
  width: 100px;
  height: 100%;
  max-height: 100px;
  /* padding-top: 1em; */
  top: 10px;
  left: 10px;
  background: #f9f9f9;
  position: absolute;
  border-radius: 50%;
}

._meet_act_btn {
  border: none;
  padding: 10px;
  height: 40px;
  margin: 0 4px;
}

._video_intro.d-flex {
  position: absolute;
  bottom: 5%;
  left: -5%;
}
._video_intro button {
  font-size: 0.85em;
  border-radius: 8px;
  background: #3a394354;
  color: #ffffff;
}
._large_video .video-img {
  position: absolute;
  top: 1%;
  right: 1%;
  width: 150px;
  height: 150px;
}

._large_video .own-video {
  position: absolute;
  top: 5%;
  right: 1%;
  width: 150px;
  height: 150px;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  border-radius: 6px;
  box-shadow: 0 0 7px 2px #0000004f;
  border: 2px solid #89898973;
}
.videoModal {
  background: #0000001e;
}

._large_video .remote_video {
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg); /* Safari and Chrome */
  -moz-transform: rotateY(180deg); /* Firefox */
}
