.chat-leftsidebar {
	 background-color: #eff2f7;
	 border-radius: 0.25rem 0 0 0.25rem;
	 box-shadow: 0 2px 4px #000;
}
 @media (min-width: 992px) {
	 .chat-leftsidebar {
		 min-width: 360px;
	}
}
 .chat-leftsidebar .chat-leftsidebar-nav .nav {
	 background-color: #74788d;
}
 .chat-leftsidebar .chat-leftsidebar-nav .nav .nav-link.active {
	 background-color: #eff2f7;
	 color: #5664d2;
}
 .chat-noti-dropdown.active:before {
	 content: "";
	 position: absolute;
	 width: 8px;
	 height: 8px;
	 background-color: #ff3d60;
	 border-radius: 50%;
	 right: 0;
}
 .chat-noti-dropdown .btn {
	 padding: 0px;
	 box-shadow: none;
	 font-size: 16px;
}
 .chat-search-box .form-control {
	 border: 0;
}
 .chat-list {
	 margin: 0;
}
 .chat-list li.active a {
	 background-color: #74788d;
}
 .chat-list li a {
	 display: block;
	 padding: 14px 16px;
	 color: #74788d;
	 transition: all 0.4s;
	 border-top: 1px solid #eff2f7;
	 border-radius: 4px;
}
 .chat-list li a:hover {
	 background-color: #74788d;
}
 .chat-list li .user-img {
	 position: relative;
}
 .chat-list li .user-img .user-status {
	 width: 10px;
	 height: 10px;
	 background-color: #74788d;
	 border-radius: 50%;
	 border: 2px solid #eff2f7;
	 position: absolute;
	 right: 0;
	 bottom: 0;
}
 .chat-list li .user-img.online .user-status {
	 background-color: #1cbb8c;
}
 .user-chat {
	 background-color: #eff2f7;
	 box-shadow: 0 2px 4px #000;
}
 .user-chat .user-chat-border {
	 border-bottom: 1px solid #eff2f7;
}
 .user-chat-nav .dropdown .nav-btn {
	 height: 36px;
	 width: 36px;
	 line-height: 36px;
	 box-shadow: none;
	 padding: 0;
	 font-size: 20px;
	 border-radius: 50%;
}
 .user-chat-nav .dropdown .dropdown-menu {
	 box-shadow: 0 2px 4px #000;
	 border: 1px solid #eff2f7;
}
 .chat-conversation li {
	 clear: both;
}
 .chat-conversation .chat-avatar {
	 float: left;
	 margin-right: 8px;
}
 .chat-conversation .chat-avatar img {
	 width: 36px;
	 height: 36px;
	 border-radius: 50%;
}
 .chat-conversation .chat-day-title {
	 position: relative;
	 text-align: center;
	 margin-bottom: 24px;
	 margin-top: 12px;
}
 .chat-conversation .chat-day-title .title {
	 background-color: #eff2f7;
	 position: relative;
	 z-index: 1;
	 padding: 3px 16px;
	 border-radius: 30px;
}
 .chat-conversation .chat-day-title:before {
	 content: "";
	 position: absolute;
	 width: 100%;
	 height: 1px;
	 left: 0;
	 right: 0;
	 background-color: #eff2f7;
	 top: 10px;
}
 .chat-conversation .chat-day-title .badge {
	 font-size: 12px;
}
 .chat-conversation .conversation-list {
	 margin-bottom: 24px;
	 display: inline-block;
	 position: relative;
}
 .chat-conversation .conversation-list .ctext-wrap {
	 overflow: hidden;
}
 .chat-conversation .conversation-list .ctext-wrap .conversation-name {
	 font-weight: 500;
	 margin-bottom: 7px;
}
 .chat-conversation .conversation-list .ctext-wrap-content {
	 padding: 12px 16px;
	 background-color: #5664d2;
	 border-radius: 0.25rem;
	 color: #fff;
}
 .chat-conversation .conversation-list .chat-time {
	 margin-top: 7px;
	 font-size: 12px;
	 text-align: right;
}
 .chat-conversation .right .conversation-list {
	 float: right;
}
 .chat-conversation .right .conversation-list .conversation-name {
	 text-align: right;
}
 .chat-conversation .right .conversation-list .ctext-wrap-content {
	 background-color: #eff2f7;
	 text-align: right;
	 color: #74788d;
}
 .chat-conversation .right .conversation-list .chat-time {
	 text-align: left;
}
 .chat-input-section {
	 background-color: #f9f9fa;
	 border-radius: 0.25rem;
}
 .chat-input {
	 background-color: #eaedf3 !important;
	 border-color: #eff2f7 !important;
}
 .chat-input-links {
	 position: absolute;
	 left: 16px;
	 top: 50%;
	 transform: translateY(-50%);
}
 .chat-input-links li a {
	 font-size: 16px;
	 line-height: 36px;
	 padding: 0px 4px;
	 display: inline-block;
}
 @media (max-width: 575.98px) {
	 .chat-send {
		 min-width: auto;
	}
}
 
